import React from "react";
import "./style.css";

class App extends React.Component {
  state = { choose: 0 };

  resultB = e => {
    e.preventDefault();
    let year = "Zysk roczny: ";
    let mnth = "Zysk miesięczny: ";

    if (
      document.getElementById("sprice").value &&
      document.getElementById("percent").value
    ) {
      year +=
        Math.round(
          ((document.getElementById("sprice").value *
            document.getElementById("percent").value) /
            100) *
            0.81 *
            100
        ) / 100;
      mnth +=
        Math.round(
          ((((document.getElementById("sprice").value *
            document.getElementById("percent").value) /
            100) *
            0.81) /
            12) *
            100
        ) / 100;
    } else {
      year = "";
      mnth = "";
    }
    document.getElementById(
      "result_bank"
    ).innerHTML = `<div>${year}</div><div>${mnth}</div>`;
  };
  resultG = e => {
    e.preventDefault();
    let kmsltrs = "Spalanie: ";

    if (
      document.getElementById("litres").value &&
      document.getElementById("km").value
    ) {
      kmsltrs +=
        Math.round(
          (document.getElementById("litres").value /
            document.getElementById("km").value) *
            100 *
            100
        ) / 100;
      kmsltrs += "l/100km";
    } else {
      kmsltrs = "";
    }

    let cost = "Koszt: ";

    if (document.getElementById("gprice").value) {
      cost +=
        Math.round(
          document.getElementById("litres").value *
            document.getElementById("gprice").value *
            100
        ) / 100;
    } else {
      cost = "";
    }

    document.getElementById(
      "result_gasoline"
    ).innerHTML = `<div>${kmsltrs}</div><div>${cost}</div>`;
  };

  ppm = e => {
    e.preventDefault();

    let sex = document.getElementById("sex").value;
    let age = document.getElementById("age").value;
    let weight = document.getElementById("weight").value;
    let height = document.getElementById("height").value;
    let ppm = "";
    let one_kg = 8000 / 30;

    if (sex === "Mężczyzna") {
      ppm = 13.7 * weight + 5 * height - 6.76 * age + 66;
    } else {
      ppm = 655 + 9.6 * weight + 1.8 * height - 4.7 * age;
    }

    let i = 0;
    let kg = 0.5;
    let kcalarr = [];
    while (i < 1) {
      if (ppm * 1.2 - kg * one_kg >= ppm) {
        kcalarr.push({ kg: kg, kcal: Math.round(ppm * 1.2 - kg * one_kg) });
        kg += 0.5;
      } else {
        i = 1;
      }
    }

    document.getElementById(
      "result_kcal"
    ).innerHTML = `<div>Ilość kcal aby trzymać wagę: <b>${Math.round(
      ppm * 1.2
    )}</b></div>`;

    kcalarr.forEach(element => {
      document.getElementById(
        "result_kcal"
      ).innerHTML += `<div>Ilość kcal aby schudnąć ${element.kg}kg: <b>${element.kcal}</b></div>`;
    });

    document.getElementById(
      "result_kcal"
    ).innerHTML += `<div>Ilość kalorii Twojego PPM: <b>${Math.round(
      ppm
    )}</b></div>`;
  };

  renderCalculator() {
    if (this.state.choose === 0) {
      return (
        <form>
          <b>Kalkulator spalania: </b>
          <label>Ilość zatankowanych litrów: </label>
          <input id="litres"></input>
          <label>Ilość przejechanych kilometrów: </label>
          <input id="km"></input>
          <label>Cena paliwa za litr: </label>
          <input id="gprice"></input>
          <br />
          <button onClick={this.resultG}>Policz:</button>

          <div id="result_gasoline"></div>
        </form>
      );
    }
    if (this.state.choose === 1) {
      return (
        <form>
          <b id="title_ro">Kalkulator zysku z rachunku oszczędnościowego: </b>
          <label>Kwota startowa: </label>
          <input id="sprice"></input>
          <label>Oprocentowanie(wpisz liczbę bez %): </label>
          <input id="percent"></input>
          <br />
          <button onClick={this.resultB}>Policz:</button>

          <div id="result_bank"></div>
        </form>
      );
    }

    if (this.state.choose === 2) {
      return (
        <form>
          <b>Kalkulator BMR:</b>

          <label>Płeć</label>
          <select id="sex">
            <option value=""></option>
            <option value="Mężczyzna">mężczyzna</option>
            <option value="Kobieta">kobieta</option>
          </select>

          <label>Wiek</label>
          <input type="text" id="age" name="age" />

          <label>Waga</label>
          <input type="text" id="weight" name="weight" />

          <label>Wzrost</label>
          <input type="text" id="height" name="height" />

          <br />
          <button id="button" onClick={this.ppm}>
            Zatwierdź
          </button>

          <div id="result_kcal"></div>
        </form>
      );
    }
  }
  onClick = () => {
    if (
      document.getElementById("menu").style.width === "400px" ||
      document.getElementById("menu").style.width === "200px"
    ) {
      document.getElementById("menu").style.width = "20px";
      document.getElementById("menu").style.transitionDuration = "0.5s";
      document.getElementById("calculator_1").style.display = "none";
      document.getElementById("calculator_1").style.transitionDuration = "0.5s";
      document.getElementById("calculator_2").style.display = "none";
      document.getElementById("calculator_2").style.transitionDuration = "0.5s";
      document.getElementById("instruction").style.display = "flex";
      document.getElementById("calculator_3").style.display = "none";
      document.getElementById("calculator_3").style.transitionDuration = "0.5s";
      document.getElementById("prev").style.display = "none";
    } else {
      if (window.innerWidth < 600) {
        document.getElementById("menu").style.width = "200px";
      } else {
        document.getElementById("menu").style.width = "400px";
      }
      document.getElementById("menu").style.transitionDuration = "0.5s";
      document.getElementById("calculator_1").style.display = "flex";
      document.getElementById("calculator_1").style.transitionDuration = "0.5s";
      document.getElementById("calculator_2").style.display = "flex";
      document.getElementById("prev").style.display = "flex";
      document.getElementById("calculator_2").style.transitionDuration = "0.5s";
      document.getElementById("calculator_3").style.display = "flex";
      document.getElementById("calculator_3").style.transitionDuration = "0.5s";
      document.getElementById("instruction").style.display = "none";
    }
  };

  choose_0 = () => {
    this.setState({ choose: 0 });
  };

  choose_1 = () => {
    this.setState({ choose: 1 });
  };
  choose_2 = () => {
    this.setState({ choose: 2 });
  };

  render() {
    return (
      <div className="App">
        <div className="navbar">Kalkulatory</div>
        <div className="main">
          <div className="calc">{this.renderCalculator()}</div>
          <div className="menu" id="menu">
            <div id="instruction" onClick={this.onClick}>
              <div>R</div>
              <div>O</div>
              <div>Z</div>
              <div>W</div>
              <div>I</div>
              <div>Ń</div>
            </div>
            <div
              id="calculator_1"
              onClick={this.choose_0}
              className="calculator"
            >
              Kalkulator spalania
            </div>
            <div
              id="calculator_2"
              onClick={this.choose_1}
              className="calculator"
            >
              Kalkulator zysku z rachunku oszczędnościowego
            </div>

            <div
              id="calculator_3"
              onClick={this.choose_2}
              className="calculator"
            >
              Kalkulator kalorii
            </div>

            <div id="prev" onClick={this.onClick}>
              W S T E C Z
            </div>
          </div>
        </div>

        <footer>Mikołaj Sojka</footer>
      </div>
    );
  }
}

export default App;
